    align-items: center;
    width: 940px;
    padding-left: 10px;
    padding-right: 10px;
    background-color: transparent;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 3px;
  right: 45px;
  z-index: 2;
  display: block;
  width: 50px;
  height: 24px;
  background-color: #283645;
  cursor: pointer;
  border: none;
  padding: 0;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 5px;
    right: 45px;
  }
}
.main-nav__toggle:hover,
.main-nav__toggle:active {
  background-color: #283645;
}
@media (min-width: 960px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 21px;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}
@media (min-width: 960px) {
  .main-nav--closed .main-nav__toggle::before {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 8px 0 0 rgba(210, 40, 86, 0.3), 0 16px 0 0 rgba(210, 40, 86, 0.3);
}
.main-nav--closed .main-nav__toggle:active::before:hover {
  background-color: #D22856;
  box-shadow: 0 8px 0 0 #D22856, 0 16px 0 0 #D22856;
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  left: auto;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 32px;
  right: 16px;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
}
@media (min-width: 660px) {
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    top: 52px;
  }
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}
.main-nav--nojs .main-nav__item {
  display: block;
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  border-top: 1px solid #000000;
}
@media (min-width: 960px) {
